//Параметры шрифта
$font-primary: "Arial", sans-serif;
$font-secondary: "Oswald", sans-serif;
$font-regular: 400;
$font-medium: 500;
$font-bold: 700;
$font-color: #000000;
$font-color-inverted: #ffffff;

//Фон
$bg-color-primary: #ffffff;
$bg-color-secondary: #68b738;

// Кнопки
$button-color: #ffffff;
$button-bg-color: #68b738;

//Экраны
$min-tablet: 768px;
$min-desktop: 1190px;

//Переменные CSS
:root {
  --hero-bg-color: #62a53b;
  --nav-menu-index-color: #{$font-color-inverted};
  --nav-menu-index-bgcolor: #{$bg-color-secondary};
  --nav-menu-color: #{$font-color};
  --nav-menu-bgcolor: #{$bg-color-primary};
  --font-color-darken: #222222;
  --font-color-lighten: #444444;
}
