@charset "UTF-8";
:root {
  --hero-bg-color: #62a53b;
  --nav-menu-index-color: #ffffff;
  --nav-menu-index-bgcolor: #68b738;
  --nav-menu-color: #000000;
  --nav-menu-bgcolor: #ffffff;
  --font-color-darken: #222222;
  --font-color-lighten: #444444;
}

@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

